export default {
    name: 'Toast',
    props: {
        'message': { type: String, required: true },
        'type': { type: String, required: true },
        'onClose': { type: Function, required: true },
        },
    computed: {
        messageClass: function() {
            return 'alert-'+this.$props.type;
        }
    }
  };



